.list__books{
    list-style: none;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: baseline;
    width: 100%;
    margin: auto;
}

@media only screen and (min-width: 767px) {
    .list__books {
        width: 80%;
    }
  }
.fa-plus-circle{
    font-size: 50px;
    color:#10887e;
    margin-right: 80px;
}
.message{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    height: 60vh;
    color: #757575;
}
#shelf{
height: 50px;
width: 100%;
}
.item__book {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  border: solid 1px lightgray;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  width: 100%;
  margin: 2%;
  height: 350px;
  background-color: #e6e6e6;
}

@media only screen and (min-width: 767px) {
  .item__book {
    width: 44%;
    height: 450px;
  }
}

@media only screen and (min-width: 1020px) {
  .item__book {
    width: 22%;
  }
}

.container_img-book {
  width: 100%;
  height: 70%;
  background-repeat: no-repeat;
  background-size: container;
  background-position: center;
}

.info__book {
  padding: 10%;
  text-align: left;
  height: 25%;
  width: 100%;
}

.name__book {
  font-size: 14px;
}
.authors__book {
  font-size: 14px;
  color: gray;
}

.fa-chevron-circle-down {
  font-size: 50px;
  color: #10887e;
}

.container__search {
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  color: white;
}

.fa-book {
  color: gray;
  font-size: 25px;
  margin-left: 5px;
}

#filterQuery {
  background-color: white;
  width: 85%;
  padding: 0 5%;
  height: 100%;
  font-size: 18px;
  border: none;
  text-align: center;
  color: grey;
  height: 100%;
  border: solid 1px lightgray;
}

@media only screen and (min-width: 767px) {
  .fa-book {
    font-size: 30px;
    width: 10%;
    margin-left: 20px;
  }

  #filterQuery {
    font-size: 24px;
  }
}

@media only screen and (min-width: 1020px) {
  .fa-book {
    width: 15%;
    margin-left: 60px;
  }
}

.container__title-app {
  background-color: #10887e;
  padding: 20px;
  color: white;
}
.title__app {
  font-weight: 400;
}

.title__shelf {
  padding: 60px 0 5px 80px;
  border-bottom: solid 2px lightgray;
  text-align: left;
  color: #757575;
  font-size: 22px;
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Krub', sans-serif;
}


